
.infoWrap{
    display: flex;
    justify-content: space-between;
    border-bottom: 2px solid black;
    align-items: center;
    padding: 20px;
    margin: 0 15px;
    height: 200px;

.info{
    display: flex;
    flex-direction: column;
    width: 100%;
.name{
    padding: 10px 0;
}
}

.iconsWrap{
    display: flex;


}
}

.icons{
    width: 40px;
    height: 40px;
    background: rgba(0,0,0,0.2);
    border-radius: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 20px;
    cursor: pointer;
&:hover{
     background: rgba(0,0,0,0.1);
 }
}